<template>
  <div>
    <el-row>
      <el-col :span="10">
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="活动名称" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
          <el-form-item label="活动区域" prop="region">
            <el-select v-model="ruleForm.region" placeholder="请选择活动区域">
              <el-option label="区域一" value="区域一"></el-option>
              <el-option label="区域二" value="区域二"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="活动时间" required>
            <el-col :span="11">
              <el-form-item prop="date1">
                <el-date-picker
                  placeholder="选择日期"
                  v-model="date1"
                  value-format="yyyy-MM-dd"
                  style="width: 100%"
                ></el-date-picker>
              </el-form-item>
            </el-col>
            <el-col class="line" :span="2">-</el-col>
            <el-col :span="11">
              <el-form-item prop="date2">
                <el-time-picker
                  placeholder="选择时间"
                  v-model="date2"
                  value-format="hh:mm:ss"
                  style="width: 100%"
                ></el-time-picker>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item label="即时配送" prop="delivery">
            <el-switch v-model="ruleForm.delivery"></el-switch>
          </el-form-item>
          <el-form-item label="活动性质" prop="type">
            <el-checkbox-group v-model="ruleForm.type">
              <el-checkbox label="美食/餐厅线上活动" name="type"></el-checkbox>
              <el-checkbox label="地推活动" name="type"></el-checkbox>
              <el-checkbox label="线下主题活动" name="type"></el-checkbox>
              <el-checkbox label="单纯品牌曝光" name="type"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="特殊资源" prop="resource">
            <el-radio-group v-model="ruleForm.resource">
              <el-radio label="线上品牌商赞助"></el-radio>
              <el-radio label="线下场地免费"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="活动形式" prop="desc">
            <el-input type="textarea" v-model="ruleForm.desc"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"
              >立即创建</el-button
            >
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="10">
        <div v-show="flag">
          <h3>活动内容</h3>
          <div>活动名称: {{ ruleForm.name }}</div>
          <div>活动区域: {{ ruleForm.region }}</div>
          <div>活动时间: {{ date1 }} ---- {{ date2 }}</div>
          <div>即时配送: {{ delivery }}</div>
          <div>
            活动性质:
            <span v-for="(k, i) in ruleForm.type" :key="i"
              >&nbsp;{{ k }}&nbsp;</span
            >
          </div>
          <div>特殊资源: {{ ruleForm.resource }}</div>
          <div>活动形式: {{ ruleForm.desc }}</div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>


<script>
import indexVue from "../0323/index.vue";
export default {
  data() {
    return {
      ruleForm: {
        name: "",
        region: "",
        delivery: false,
        type: [],
        resource: "",
        desc: "",
        date1: "",
        date2: "",
      },
      flag: false,
      delivery: false,
      date1: "",
      date2: "",
      rules: {
        name: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" },
        ],
        region: [
          { required: true, message: "请选择活动区域", trigger: "change" },
        ],
        type: [
          {
            type: "array",
            required: true,
            message: "请至少选择一个活动性质",
            trigger: "change",
          },
        ],
        resource: [
          { required: true, message: "请选择活动资源", trigger: "change" },
        ],
        desc: [{ required: true, message: "请填写活动形式", trigger: "blur" }],
      },
    };
  },
  methods: {
    submitForm(formName) {
      indexVue.__file = "2021/0328/submit/" + this.ruleForm;
      this.$refs[formName].validate((valid) => {
        this.flag = false;
        if (valid) {
          console.log(valid);
          this.flag = valid;
          if (this.ruleForm.delivery) {
            this.delivery = "是";
          } else {
            this.delivery = "否";
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.flag = false;
      this.$refs[formName].resetFields();
    },
  },
  watch: {
    ruleForm: {
      handler: function () {
        this.flag = false;
      },
      deep: true,
    }
  },
};
</script>